import { useState, useCallback, useMemo } from "react";
import { toast } from "sonner";
import { generateImage, publishToInstagram } from "@/services/api";

// Constantes para valores del formulario
export const PRIORITY_LIMITS = {
  MIN: 1,
  MAX: 10,
  DEFAULT_POSITION: 5,
  DEFAULT_LOCATION: 3,
  DEFAULT_EMAIL: 3,
  DEFAULT_REQUIREMENTS: 4
};

export interface FormData {
  position: string;
  positionPriority: number;
  location: string;
  locationPriority: number;
  email: string;
  emailPriority: number;
  requirements: string;
  requirementsPriority: number;
  image: string | null;
}

export const INITIAL_FORM_DATA: FormData = {
  position: "",
  positionPriority: PRIORITY_LIMITS.DEFAULT_POSITION,
  location: "",
  locationPriority: PRIORITY_LIMITS.DEFAULT_LOCATION,
  email: "",
  emailPriority: PRIORITY_LIMITS.DEFAULT_EMAIL,
  requirements: "",
  requirementsPriority: PRIORITY_LIMITS.DEFAULT_REQUIREMENTS,
  image: null,
};

export default function useJobPostingForm() {
  const [formData, setFormData] = useState<FormData>(INITIAL_FORM_DATA);
  const [previewGenerated, setPreviewGenerated] = useState(false);
  const [showConfirmation, setShowConfirmation] = useState(false);
  const [isGenerating, setIsGenerating] = useState(false);
  const [isPublishing, setIsPublishing] = useState(false);

  // Determinar si el formulario es válido
  const isFormValid = useMemo(() => {
    return Boolean(formData.position && formData.location && formData.email);
  }, [formData.position, formData.location, formData.email]);

  // Manejar cambios en los inputs de texto
  const handleInputChange = useCallback((e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  }, []);

  // Manejar cambios en los sliders
  const handleSliderChange = useCallback((name: string, value: number[]) => {
    setFormData((prev) => ({ ...prev, [name]: value[0] }));
  }, []);

  // Manejar la subida de imágenes
  const handleImageUpload = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;
    
    // Validar el tamaño del archivo (máximo 5MB)
    if (file.size > 5 * 1024 * 1024) {
      toast.error("Archivo demasiado grande", {
        description: "La imagen no debe superar los 5MB."
      });
      return;
    }

    const reader = new FileReader();
    reader.onload = () => {
      setFormData((prev) => ({ ...prev, image: reader.result as string }));
    };
    reader.readAsDataURL(file);
  }, []);

  // Generar el post
  const generatePost = useCallback(async () => {
    if (!isFormValid) {
      toast.error("Campos requeridos", {
        description: "Por favor complete los campos obligatorios: Puesto, Ubicación y Email."
      });
      return;
    }

    setIsGenerating(true);
    
    try {
      // Convertir la imagen de base64 a File si existe
      let imageFile = null;
      if (formData.image && formData.image.startsWith('data:')) {
        const response = await fetch(formData.image);
        const blob = await response.blob();
        imageFile = new File([blob], "image.jpg", { type: "image/jpeg" });
      }
      
      // Llamar al servicio API real
      const response = await generateImage({
        perfil: formData.position,
        ubicacion: formData.location,
        email: formData.email,
        requisitos: formData.requirements,
        imagen: imageFile
      });
      
      if (response.success) {
        setPreviewGenerated(true);
        toast.success("Post generado", {
          description: "La vista previa ha sido generada correctamente."
        });
      } else {
        throw new Error(response.message);
      }
    } catch (error) {
      toast.error("Error", {
        description: error instanceof Error ? error.message : "No se pudo generar la vista previa. Intente nuevamente."
      });
    } finally {
      setIsGenerating(false);
    }
  }, [formData, isFormValid]);

  // Reiniciar el formulario
  const resetForm = useCallback(() => {
    setFormData(INITIAL_FORM_DATA);
    setPreviewGenerated(false);
    setShowConfirmation(false);
    toast.info("Formulario reiniciado", {
      description: "Todos los campos han sido reiniciados."
    });
  }, []);

  // Manejar la publicación
  const handlePublish = useCallback(() => {
    setShowConfirmation(true);
  }, []);

  // Confirmar la publicación
  const confirmPublish = useCallback(async (publish: boolean) => {
    setShowConfirmation(false);
    
    if (publish) {
      setIsPublishing(true);
      try {
        // Llamar al servicio API real
        const response = await publishToInstagram({
          image_url: "ruta_a_la_imagen_generada", // Esto debería venir de la respuesta de generateImage
          caption: `Buscamos: ${formData.position} en ${formData.location}. Envía tu CV a ${formData.email}. #empleo #trabajo #${formData.position.replace(/\s+/g, '')}`
        });
        
        if (response.success) {
          toast.success("Publicado con éxito", {
            description: "La oferta laboral ha sido publicada en Instagram."
          });
        } else {
          throw new Error(response.message);
        }
      } catch (error) {
        toast.error("Error al publicar", {
          description: error instanceof Error ? error.message : "No se pudo publicar en Instagram. Intente nuevamente."
        });
      } finally {
        setIsPublishing(false);
      }
    } else {
      toast.info("Guardado", {
        description: "La oferta laboral ha sido guardada para más tarde."
      });
    }
  }, [formData]);

  return {
    formData,
    previewGenerated,
    showConfirmation,
    isGenerating,
    isPublishing,
    isFormValid,
    handleInputChange,
    handleSliderChange,
    handleImageUpload,
    generatePost,
    resetForm,
    handlePublish,
    confirmPublish,
  };
}