/**
 * Servicio API para interactuar con el backend
 */

// Configuración base para la API
const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000/api';

// Tipos de datos para la API
export interface GenerateImageRequest {
  perfil: string;
  ubicacion: string;
  email: string;
  requisitos?: string;
  imagen?: File | null;
}

export interface GenerateImageResponse {
  success: boolean;
  message: string;
  image_url?: string;
}

export interface PublishRequest {
  image_url: string;
  caption?: string;
}

export interface PublishResponse {
  success: boolean;
  message: string;
  post_id?: string;
}

/**
 * Servicio para generar una imagen de oferta laboral
 */
export async function generateImage(data: GenerateImageRequest): Promise<GenerateImageResponse> {
  try {
    const formData = new FormData();
    formData.append('perfil', data.perfil);
    formData.append('ubicacion', data.ubicacion);
    formData.append('email', data.email);
    
    if (data.requisitos) {
      formData.append('requisitos', data.requisitos);
    }
    
    if (data.imagen) {
      formData.append('imagen', data.imagen);
    }
    
    const response = await fetch(`${API_BASE_URL}/generate-image`, {
      method: 'POST',
      body: formData,
    });
    
    if (!response.ok) {
      throw new Error(`Error en la respuesta: ${response.status}`);
    }
    
    return await response.json();
  } catch (error) {
    console.error('Error al generar la imagen:', error);
    return {
      success: false,
      message: error instanceof Error ? error.message : 'Error desconocido',
    };
  }
}

/**
 * Servicio para publicar en Instagram
 */
export async function publishToInstagram(data: PublishRequest): Promise<PublishResponse> {
  try {
    const formData = new FormData();
    formData.append('image_url', data.image_url);
    
    if (data.caption) {
      formData.append('caption', data.caption);
    }
    
    const response = await fetch(`${API_BASE_URL}/publish-to-instagram`, {
      method: 'POST',
      body: formData,
    });
    
    if (!response.ok) {
      throw new Error(`Error en la respuesta: ${response.status}`);
    }
    
    return await response.json();
  } catch (error) {
    console.error('Error al publicar en Instagram:', error);
    return {
      success: false,
      message: error instanceof Error ? error.message : 'Error desconocido',
    };
  }
}

